<template>
  <div id="employee-table">
    <table>
      <thead>
        <th>Employee name</th>
        <th>Employee email</th>
        <th>Actions</th>
      </thead>
      <tbody>
        <tr v-for="employee in employees" :key="employee.id">
          <td v-if="editing === employee.id">
            <input type="text" v-model="employee.name" />
          </td>
          <td v-else>{{employee.name}}</td>
          <td v-if="editing === employee.id">
            <input type="text" v-model="employee.email" />
          </td>
          <td v-else>{{employee.email}}</td>
          <td v-if="editing === employee.id">
            <button @click="editEmployee(employee)">Save</button>
            <button class="muted-button" @click="cancelEdit(employee)">Cancel</button>
          </td>
          <td v-else>
            <button @click.prevent="editMode(employee)">Edit</button>
            <button
              class="muted-button"
              @click.prevent="$emit('delete:employee', employee.id)"
            >Delete</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "employee-table",
  data() {
    return {
      editing: null
    };
  },
  props: {
    employees: {
      type: Array,
      required: true
    }
  },
  methods: {
    editMode(employee) {
      this.cachedEmployee = Object.assign({}, employee);
      this.editing = employee.id;
    },
    editEmployee(employee) {
      if (employee.name === "" || employee.email === "") return;
      const { name: updatedName, email: updatedEmail } = employee;
      const { name: oldName, email: oldEmail } = this.cachedEmployee;
      if (oldName === updatedName && oldEmail === updatedEmail) return;

      this.$emit("edit:employee", employee.id, employee, this.cachedEmployee);
      this.editing = false;
    },
    cancelEdit(employee) {
      Object.assign(employee, this.cachedEmployee);
      this.editing = null;
    }
  }
};
</script>

<style scoped>
button {
  margin: 0 0.5rem 0 0;
}
</style>
